/* card-add */
.card-add{
	font-size: 14px;
	color: #444;
	border: 0;
	outline: none;
	float: left;
	padding: 0 0 0 30px;
	position: relative;
	background: transparent;
	@nest &:after{
		position: absolute;
		content: '';
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		background: resolve('add.png');
		width: width('add.png');
		height: height('add.png');
	}
	@nest &:hover{
		text-decoration: underline;
	}
	@media (max-width: 512px){
		float: none;
	}
}