/* backet-content */
.backet-content{
	@nest &__bottom{
		width: 100%;
		float: right;
		max-width: 390px;
		padding: 17px 0 20px;
		@nest &-result{
			font-size: 24px;
			color: #444;
			margin-right: 35px;
			font-family: var(--font2);
			text-transform: uppercase;
			display: inline-block;
			vertical-align: middle;
		}
		@nest &-price{
			font-size: 24px;
			color: #d94893;
			font-weight: 700;
			display: inline-block;
			vertical-align: middle;
		}
	}
}