/* steps-block */
.steps-block{
	padding-left: 125px;
	position: relative;
	@media (max-width: 1199px){
		margin-top: 30px;
	}
	@nest &__image{
		position: absolute;
		left: 0;
		top: 0;
	}
	@nest &__title{
		font-size: 20px;
		color: #727272;
		font-weight: 600;
		letter-spacing: -1px;
		text-transform: uppercase;
		margin: 0 0 20px;
	}
	@nest &__text{
		font-size: 13px;
		color: #606060;
		margin: 0;
		letter-spacing: -.65px;
		text-transform: uppercase;
	}
}