/* dropdown-list */
.dropdown-list{
	position: absolute;
	content: '';
	top: 100%;
	left: 0;
	margin: 0;
	background: #fff;
	display: none;
  z-index: 20;
	list-style: none;
	padding: 20px 20px 17px;
  box-shadow: 0px 5px 7px 0px rgba(0, 0, 0, 0.26);
  @media (max-width: 991px){
    left: 50%;
    transform: translateX(-50%);
  }
  @nest &__item{
  	margin-bottom: 13px;
  	@media (max-width: 1199px){
  		margin-bottom: 3px;
  	}
  }
  @nest &__link{
  	font-size: 15px;
  	color: #000;
  	display: inline-block;
  	text-decoration: none;
  	@nest &:hover{
  		text-decoration: underline;
  	}
  	@media (max-width: 1199px){
  		font-size: 12px;
  	}
    @media (max-width: 991px){
      font-size: 15px;
    }
  }
}