/* card-btn */
.card-btn{
	font-size: 16px;
	color: #fff;
	min-height: 42px;
	border: 0;
	width: 100%;
	max-width: 240px;
	float: right;
	outline: none;
	background: #915189;
	border-radius: 25px;
	transition: opacity 300ms;
	@nest &:hover{
		opacity: .7;
	}
	@media (max-width: 512px){
		float: none;
		margin-top: 15px;
	}
	@nest &--sm{
		width: 160px;
	}
}