@import "normalize-css/normalize.css";
@import "slick-carousel/slick/slick.css";
@import "fancybox/source/jquery.fancybox.css";
@import "ion.rangeSlider/css/ion.rangeSlider.skinHTML5.css";
@import "ion.rangeSlider/css/ion.rangeSlider.css";
/*! main */
@font-face {
    font-family: 'pt_serifregular';
    src: url('../fonts/8154.woff2') format('woff2'),
         url('../fonts/8154.woff') format('woff'),
         url('../fonts/8154.ttf') format('truetype'),
         url('../fonts/8154.svg#pt_serifregular') format('svg');
    font-weight: 400;
    font-style: normal;
}
@font-face {
    font-family: 'pt_serifregular';
    src: url('../fonts/8155.woff2') format('woff2'),
         url('../fonts/8155.woff') format('woff'),
         url('../fonts/8155.ttf') format('truetype'),
         url('../fonts/8155.svg#pt_serifitalic') format('svg');
    font-weight: 400;
    font-style: italic;
}
@font-face {
    font-family: 'pf_agora_slab_proregular';
    src: url('../fonts/pfagoraslabpro-reg_1.woff2') format('woff2'),
         url('../fonts/pfagoraslabpro-reg_1.woff') format('woff'),
         url('../fonts/pfagoraslabpro-reg_1.ttf') format('truetype'),
         url('../fonts/pfagoraslabpro-reg_1.svg#pf_agora_slab_proregular') format('svg');
    font-weight: 400;
    font-style: normal;
}

html, body{
	font-family: 'Open Sans', sans-serif;
	font-weight: 400;
}

body{
	background: #fbfbfb;
}

:root{
    --font: 'pt_serifregular', serif;
	--font2: 'pf_agora_slab_proregular';
}
.no-padding-left{
    padding-left: 0 !important;
    @media (max-width: 767px){
        padding-left: 16px !important;
    }
}
.no-padding-right{
    padding-right: 0 !important;
}
.slick-list{
    padding-bottom: 10px;
}