/* sort */
.sort{
	padding: 5px 0 5px 18px;
	background: #915189;
	border-radius: 5px;
	@nest &__title{
		font-size: 13px;
		color: #fff;
		margin-right: 8px;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 512px){
			display: block;
			margin-bottom: 5px;
		}
	}
	@media (max-width: 767px){
		text-align: center;
	}
}