/* backet-item */
.backet-item{
	font-size: 0;
	padding: 15px 0;
	border-top: 1px solid #ededed;
	border-bottom: 1px solid #ededed;
	@nest &:last-child{
		border-top: 0;
	}
	@nest &__image{
		width: 15.5%;
		display: inline-block;
		vertical-align: middle;
		@nest &-inner{
			display: block;
			margin: auto;
			@media (max-width: 512px){
				margin-right: auto;
				margin-left: 0;
			}
		}
		@media (max-width: 991px){
			width: 30%;
		}
		@media (max-width: 512px){
			width: 100%;
		}
	}
	@nest &__text{
		width: 50.5%;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 991px){
			width: 70%;
		}
		@media (max-width: 512px){
			width: 100%;
			margin: 20px 0 0;
		}
		@nest &-title{
			font-size: 18px;
			color: #444;
			font-weight: 600;
			margin: 0 0 10px;
		}
		@nest &-desc{
			margin: 0;
			max-width: 450px;
			font-size: 16px;
			color: #383838;
			font-style: italic;
			font-family: var(--font);
		}
	}
	@nest &__select{
		width: 10.5%;
		margin: 25px 0 0;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 991px){
			width: 30%;
		}
		@media (max-width: 512px){
			display: block;
		}
		@nest &-title{
			font-size: 12px;
			color: #4b453e;
			display: block;
			margin-bottom: 5px;
		}
	}
	@nest &__price{
		width: 10%;
		font-size: 18px;
		color: #d94893;
		margin: 50px 0 0;
		font-weight: 700;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 991px){
			width: 30%;
		}
		@media (max-width: 512px){
			margin-top: 20px;
			width: 45%;
		}
		@nest &--old{
			color: #995076;
			text-decoration: line-through;
		}
	}
	@nest &__del{
		width: 3.5%;
		margin: 50px 0 0;
		display: inline-block;
		vertical-align: top;
		text-align: center;
		@media (max-width: 991px){
			width: 10%;
		}
		@media (max-width: 512px){
			margin-top: 20px;
		}
		@nest &-btn{
			border: 0;
			outline: none;
			padding: 0;
			background: transparent;
			transition: opacity 300ms;
			@nest &:hover{
				opacity: .7;
			}
		}
	}
}