/* kit-block */
.kit-block{
	z-index: 10;
	max-width: 220px;
	border-radius: 6px;
	background: #fff;
	position: relative;
	display: inline-block;
	vertical-align: top;
	padding-bottom: 25px;
	margin-right: 28px;
	box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.18);
	@media (max-width: 991px){
		margin-right: 7px;
	}
	@media (max-width: 767px){
		margin-bottom: 15px;
	}
	@media (max-width: 550px){
		display: block;
		margin: auto;
		margin-bottom: 15px;
	}
	@nest &:nth-child(1){
		z-index: 25;
		@nest & .kit-block{
			z-index: 30;
		}
	}
	@nest &:nth-child(2){
		z-index: 20;
		@nest & .kit-block{
			z-index: 25;
		}
	}
	@nest &:nth-child(3){
		margin-right: 0;
		@media (max-width: 550px){
			margin-right: auto;
		}
	}
	@nest &__image{
		min-height: 230px;
		position: relative;
		margin-bottom: 35px;
		@nest &-inner{
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}
	}
	@nest &__title{
		font-size: 18px;
		color: #323232;
		font-weight: 600;
		padding-right: 10px;
		padding-left: 25px;
		min-height: 38px;
		@media (max-width: 1199px){
			padding-left: 10px;
		}
	}
	@nest &__price{
		font-size: 18px;
		color: #d94893;
		font-weight: 800;
		padding-left: 25px;
		@media (max-width: 1199px){
			padding-left: 0;
		}
	}
}