/* card-desc */
.card-desc{
	padding: 20px;
	margin: 20px 0 0;
	border-radius: 5px;
	background: #f3f3f3;
	@nest &__text{
		font-size: 15px;
		color: #444;
		margin: 0 0 20px;
		@nest &:last-child{
			margin-bottom: 0;
		}
		@media (max-width: 767px){
			font-size: 14px;
		}
	}
	@nest &-bot{
		margin: 20px 0 0;
		font-size: 15px;
		color: #444;
		@media (max-width: 767px){
			font-size: 14px;
		}
	}
}