/* lk-top-title */
.lk-top-title{
	max-width: 440px;
	margin: auto;
	clear: fix;
	padding: 20px 0;
	@media (max-width: 767px){
		max-width: 255px;
	}
	@nest &__link{
		font-size: 30px;
		color: #bcbcbc;
		float: left;
		position: relative;
		text-decoration: none;
		font-family: var(--font2);
		transition: color 300ms;
		@nest &:before{
			position: absolute;
			content: '';
			right: -50px;
			top: 50%;
			height: 16px;
			width: 1px;
			background: #acacac;
			transform: translateY(-50%);
		}
		@nest &:last-child{
			float: right;
			@nest &:before{
				display: none;
			}
		}
		@nest &--active{
			color: #915189;
			@nest &:after{
				position: absolute;
				content: '';
				left: 50%;
				bottom: -22px;
				transform: translateX(-50%);
				background: resolve('lk.png');
				width: width('lk.png');
				height: height('lk.png');
			}
		}
		@nest &:hover{
			color: #915189;
		}
		@media (max-width: 767px){
			font-size: 15px;
		}
	}
}