/* filter */
.filter{
  width: 100%;
	padding: 0 25px 20px;
  border-radius: 6px;
  background: #fff;
  margin: 15px 0 0;
  max-width: 225px;
  display: inline-block;
  vertical-align: top;
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.18);
  @media (max-width: 991px){
    margin: 0;
  }
  @media (max-width: 512px){
    display: block;
    margin: auto;
    margin-top: 15px;
  }
  @nest &__title{
  	font-size: 14px;
  	color: #2e2e2e;
  	font-weight: 700;
  	margin: 0 0 15px;
  	padding-top: 20px;
  }
}