/* btn-search */
.btn-search{
	position: absolute;
	content: '';
	right: 0;
	top: 0;
	bottom: 0;
	width: 49px;
	border: 0;
	outline: none;
	padding: 0;
	background: #883e7f;
	transition: background 300ms;
	@nest &:after{
		position: absolute;
		content: '';
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		background: resolve('search.png');
		width: width('search.png');
		height: height('search.png');
	}
	@nest &:hover{
		background: #6a2562;
	}
	@media (max-width: 991px){
		height: 49px;
		position: relative;
		margin: auto;
		display: block;
		margin-top: 10px;
	}
}