/* card-image */
.card-image{
	margin-bottom: 40px;
	@nest &__lg{
		min-height: 440px;
	}
	@nest &__sm{
		width: 114px;
		height: 92px;
		cursor: pointer;
		position: relative;
		display: inline-block;
		vertical-align: top;
		@nest &--active{
			border: 2px solid #dcdcdc;
		}
		@nest & img{
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}
	}
}