/* header */
.header{
	font-size: 0;
	padding: 0 0 10px;
	background: #fbfbfb;
	@nest &__number{
		font-size: 18px;
		color: #373737;
		font-weight: 600;
		position: relative;
		margin: 9px 0 5px;
		@nest &:after{
			position: absolute;
			content: '';
			left: -25px;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('tel.png');
			width: width('tel.png');
			height: height('tel.png');
		}
	}
	@media (max-width: 1199px){
		text-align: center;
	}
}