/* history-hidden */
.history-hidden{
	display: none;
	background: #fbfbfb;
	position: relative;
	padding: 90px 0 70px;
	@nest &:after{
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		right: 0;
		width: 100%;
		background: resolve('shadow.png') no-repeat top center;
		height: height('shadow.png');
	}
}