/* card-info */
.card-info{
	margin: 20px 0 0;
	@nest &__top{
		@nest &-item{
			font-size: 12px;
			color: #4b453e;
			margin-right: 30px;
			position: relative;
			display: inline-block;
			@nest &:after{
				position: absolute;
				content: '';
				top: 50%;
				width: 1px;
				height: 12px;
				background: #4b453e;
				right: -16px;
				transform: translateY(-50%);
			}
			@nest &:last-child{
				@nest &:after{
					display: none;
				}
			}
		}
	}
	@nest &__title{
		font-size: 48px;
		color: #444;
		margin: 20px 0 0;
		font-family: var(--font2);
		@media (max-width: 767px){
			font-size: 30px;
		}
		@media (max-width: 512px){
			font-size: 15px;
		}
	}
	@nest &__desc{
		font-size: 16px;
		color: #383838;
		font-style: italic;
		margin: 13px 0;
		@media (max-width: 767px){
			font-size: 14px;
		}
	}
	@nest &__price{
		font-size: 30px;
		color: #d94893;
		font-weight: 800;
		@media (max-width: 767px){
			font-size: 20px;
		}
	}
}