/* card-block */
.card-block{
	background: #fbfbfb;
	position: relative;
	margin-bottom: 10px;
	padding: 30px 30px 20px 195px;
	@media (max-width: 1232px){
		margin-top: 20px;
	}
	@media (max-width: 512px){
		padding: 15px;
	}
	@nest &--bg{
		padding-left: 0;
		background: transparent;
	}
	@nest &__image{
		position: absolute;
		content: '';
		left: 0;
		top: 0;
		bottom: 0;
		width: 100%;
		max-width: 195px;
		@media (max-width: 512px){
			position: static;
			margin-bottom: 10px;
		}
		@nest &-inner{
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			@media (max-width: 512px){
				position: static;
				transform: translate(0);
			}
		}
	}
	@nest &__title-lg{
		font-size: 30px;
		color: #915189;
		font-family: var(--font2);
		margin: 0 0 15px;
		@media (max-width: 512px){
			font-size: 25px;
		}
	}
	@nest &__text{
		font-size: 14px;
		color: #6b6b6b;
		line-height: 22px;
		margin: 0 0 20px;
	}
	@nest &__btn{
		font-size: 14px;
		color: #fff;
		font-weight: 600;
		padding: 4px 30px 6px;
		border-radius: 15px;
		text-decoration: none;
		background: #915189;
		transition: background 300ms;
		@nest &:hover{
			background: #663360;
		}
	}
	@nest &__title-sm{
		font-size: 16px;
		color: #acacac;
		font-family: var(--font2);
		margin: 0 0 20px;
		text-transform: uppercase;
	}
	@nest &__text-sec{
		font-size: 14px;
		color: #212121;
		line-height: 24px;
		@nest &-link{
			font-size: 14px;
			color: #915189;
			@nest &:hover{
				text-decoration: none;
			}
		}
	}
}