/* card-actions */
.card-actions{
	margin: 30px 0 0;
	@media (max-width: 1232px){
		margin-top: -20px;
	}
	@nest &__select{
		max-width: 92px;
		margin-right: 10px;
		display: inline-block;
		vertical-align: middle;
		@nest & .select{
			width: 92px;
			min-height: 37px;
			background: #f3f3f3;
			padding-left: 13px;
			border-radius: 3px;
			box-shadow: inset 0px 1px 1px 0px rgba(0, 0, 0, 0.1);
		}
		@nest & .select__current{
			color: #000;
			width: 100%;
			display: block;
			font-size: 18px;
			line-height: 37px;
			@nest &:after{
				right: 10px;
				background: resolve('tr2.png');
				width: width('tr2.png');
				height: height('tr2.png');
			}
		}
		@nest & .select__list{
			width: 100%;
			padding-left: 13px;
		}
		@nest & .select__item{
			margin: 5px 0;
			font-size: 14px;
		}
	}
	@nest &__link{
		font-size: 16px;
		font-weight: 600;
		display: inline-block;
		vertical-align: middle;
		text-decoration: none;
		border-radius: 25px;
		padding: 12px 35px;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
		@media (max-width: 1199px){
			padding: 10px 20px;
		}
		@nest &--backet{
			color: #fff;
			font-size: 14px;
			background: #915189;
			margin-right: 10px;
		}
		@nest &--order{
			color: #d94893;
			border: 2px solid #d94893;
		}
		@media (max-width: 512px){
			display: block;
			max-width: 200px;
			text-align: center;
			margin: 10px auto 10px 0;
		}
	}
}