/* reviews-top */
.reviews-top{
	clear: fix;
	margin-bottom: 25px;
	@nest &__title{
		float: left;
		width: 50%;
		font-size: 22px;
		color: #6b6b6b;
		font-weight: 600;
		@media (max-width: 1199px){
			float: none;
		}
	}
	@nest &__link{
		float: right;
		font-size: 14px;
		color: #6b6b6b;
		font-weight: 600;
		@nest &:hover{
			text-decoration: none;
		}
		@media (max-width: 1199px){
			float: none;
		}
	}
}