/* history-form */
.history-form{
	padding-right: 40px;
	padding-bottom: 20px;
	border-right: 1px solid #d5d5d5;
	@media (max-width: 991px){
		padding-right: 0;
		border-right: 0;
		margin-bottom: 20px;
	}
	@nest &__title{
		font-size: 24px;
		color: #444;
		margin: 0 0 15px;
	}
	@nest &__info{
		font-size: 16px;
		color: #444;
		display: block;
		padding: 17px 0 15px 17px;
		border-top: 1px solid #c9c9c9;
		@nest &:last-child{
			border-bottom: 1px solid #c9c9c9;
		}
		@media (max-width: 512px){
			font-size: 14px;
		}
	}
}