/* tovars-hidden */
.tovars-hidden{
	position: absolute;
	content: '';
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: 0;
	padding: 0 20px;
	text-align: center;
	transition: opacity 300ms;
	background: color(#000 alpha(53%));
	@nest &__backet{
		font-size: 16px;
		color: #fff;
		font-weight: 600;
		background: #915189;
		border-radius: 20px;
		padding: 12px 0 11px;
		text-decoration: none;
		margin-top: 60px;
		display: block;
		letter-spacing: -.8px;
		transition: background 300ms;
		@nest &:hover{
			background: #693062;
		}
	}
	@nest &__add{
		font-size: 16px;
		color: #fff;
		margin-top: 7px;
		font-weight: 600;
		padding: 12px 0 11px;
		border: 1px solid #fff;
		border-radius: 20px;
		text-decoration: none;
		display: block;
		letter-spacing: -.8px;
		transition: background 300ms;
		@nest &:hover{
			background: color(#000 alpha(30%));
		}
	}
	@nest &__desc{
		font-size: 16px;
		color: #fff;
		margin-top: 35px;
		display: inline-block;
		@nest &:hover{
			text-decoration: none;
		}
	}
}