/* tovars-top */
.tovars-top{
	clear: fix;
	margin-bottom: 20px;
	@nest &__title{
		float: left;
		color: #6b6b6b;
		font-size: 33px;
		width: 70%;
		letter-spacing: -1.65px;
		@media (max-width: 1199px){
			width: 40%;
		}
		@media (max-width: 991px){
			font-size: 25px;
		}
		@media (max-width: 512px){
			float: none;
			font-size: 20px;
		}
	}
	@nest &__link{
		font-size: 19px;
		color: #6b6b6b;
		float: right;
		margin-top: 12px;
		font-weight: 600;
		letter-spacing: -.95px;
		@nest &:hover{
			text-decoration: none;
		}
		@media (max-width: 512px){
			float: none;
		}
	}
}