/* footer-nav */
.footer-nav{
	padding: 0;
	text-align: left;
	display: inline-block;
	vertical-align: top;
	@media (max-width: 512px){
		display: block;
		margin-bottom: 30px;
	}
	@nest &--sm{
		max-width: 90px;
		margin-right: 45px;
	}
	@nest &--lg{
		max-width: 200px;
		margin-right: 30px;
	}
	@nest &--md{
		max-width: 155px;
		margin-right: 50px;
	}
	@nest &__title{
		display: block;
		font-size: 12px;
		color: #fff;
		min-height: 32px;
		font-weight: 700;
		margin-bottom: 15px;
		@media (max-width: 512px){
			min-height: 0;
		}
	}
	@nest &__item{
		margin-bottom: 5px;
	}
	@nest &__link{
		font-size: 12px;
		color: #fff;
		font-weight: 600;
		text-decoration: none;
		@nest &:hover{
			text-decoration: underline;
		}
	}
}