/* card-label */
.card-label{
	font-size: 16px;
	color: #444;
	cursor: pointer;
	padding-left: 30px;
	margin-right: 40px;
	position: relative;
	display: inline-block;
	vertical-align: middle;
	@nest &:before{
		position: absolute;
		content: '';
		left: 0;
		top: 50%;
		width: 24px;
		height: 24px;
		border-radius: 50%;
		background: #f3f3f3;
		transform: translateY(-50%);
		box-shadow: inset 0px 1px 1px 0px rgba(0, 0, 0, 0.1);
	}
	@nest &:after{
		position: absolute;
		content: '';
		left: 7px;
		top: 6px;
		width: 10px;
		height: 10px;
		border-radius: 50%;
		background: #915189;
		display: none;
	}
	@media (max-width: 767px){
		font-size: 14px;
	}
	@media (max-width: 512px){
		display: block;
		margin: 10px 0 15px;
	}
}