/* pages-nav */
.pages-nav{
	clear: both;
	margin: 25px 0 0;
	text-align: center;
	@nest &__link{
		font-size: 14px;
		color: #131313;
		margin: 0 13px;
		position: relative;
		text-decoration: none;
		display: inline-block;
		vertical-align: middle;
		@nest &:after{
			position: absolute;
			content: '';
			left: 50%;
			top: 50%;
			width: 23px;
			height: 23px;
			z-index: -1;
			opacity: 0;
			border-radius: 50%;
			background: #915189;
			transition: opacity 300ms;
			transform: translate(-50%, -50%);
		}
		@nest &:hover{
			color: #fff;
			@nest &:after{
				opacity: 1;
			}
		}
		@nest &--active{
			color: #fff;
			@nest &:after{
				opacity: 1;
			}
		}
		@nest &--btn{
			transition: opacity 300ms;
			@nest &:hover{
				opacity: .7;
			}
			@nest &:after{
				display: none;
			}
		}
		@media (max-width: 512px){
			margin: 0 10px;
		}
	}
}