/* filter-range */
.filter-range{
	font-size: 0;
	position: relative;
	padding-bottom: 25px;
	border-bottom: 1px dashed #555;
	@nest &__input{
		border: 0;
		outline: none;
		width: 66px;
		height: 24px;
		font-size: 12px;
		color: #2e2e2e;
		padding-left: 5px;
		background: #fbfbfb;
		border-radius: 2px;
		display: inline-block;
		vertical-align: middle;
		border: 1px solid #ececec;
		@nest &::placeholder{
			font-size: 12px;
			color: #2e2e2e;
			@media (max-width: 1199px){
				font-size: 13px;
			}
		}
		@nest &.irs-from{
			display: block;
		}
		@nest &.irs-to{
			display: block;
		}
		@media (max-width: 1199px){
			width: 65px;
			font-size: 13px;
		}
	}
	@nest &__mark{
		font-size: 14px;
		color: #2e2e2e;
		font-weight: 700;
		margin: 0 3px;
		display: inline-block;
		vertical-align: middle;
	}
	@nest &__rub{
		font-size: 11px;
		color: #2e2e2e;
		font-weight: 600;
		margin-left: 5px;
		display: inline-block;
		vertical-align: middle;
	}
}
.irs-slider{
	width: 18px;
	height: 18px;
	top: 28.5px;
	cursor: pointer;
	border-radius: 4px;
	border: 1px solid #bbbbbb;
	background: #f4f5f9;
	box-shadow: none;
}
.irs-line{
	width: 100%;
	height: 11px;
	border-radius: 6px;
	background: #f5f5f5;
	border: 1px solid #bfbfbf;
}
.irs-bar{
	height: 11px;
	border-radius: 6px;
	border: 1px solid #d2d2d2;
	background: linear-gradient(to top, #9c5996, #d69ad0);
}
.irs-min{
	font-size: 11px;
	color: #888;
	top: auto;
	left: 3px;
	padding: 0;
	bottom: -15px;
	background: transparent;
	visibility: visible !important;
}
.irs-max{
	font-size: 11px;
	color: #888;
	top: auto;
	right: 3px;
	padding: 0;
	bottom: -15px;
	background: transparent;
	visibility: visible !important;
}
.irs-from{
	display: none;
}
.irs-to{
	display: none;
}
.irs-single{
	display: none;
}
.js-irs-0{
	top: -10px;
}