/* info-input-wrap */
.info-input-wrap{
	width: 78%;
	display: inline-block;
	vertical-align: middle;
	@nest &__left{
		width: 57%;
		float: left;
		padding-right: 35px;
		@media (max-width: 1199px){
			width: 100%;
			float: none;
			margin-bottom: 10px;
		}
	}
	@nest &__right{
		width: 43%;
		clear: fix;
		float: right;
		@media (max-width: 1199px){
			width: 100%;
			float: none;
		}
	}
}