/* header-right */
.header-right{
	max-width: 415px;
	display: inline-block;
	vertical-align: top;
	margin: 10px 0 0;
	background: #fff;
	border-radius: 8px;
	margin-left: 50px;
  box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.18);
  @media (max-width: 1199px){
  	display: block;
  	margin: auto;
  	margin-top: 10px;
  }
}