/* lk-block */
.lk-block{
	clear: fix;
	background: #fff;
	margin: 20px 0 0;
	border-radius: 7px;
	padding: 30px 15px 10px 25px;
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.18);
  @nest &__title{
  	font-size: 14px;
  	color: #2e2e2e;
  	font-weight: 700;
  	margin: 0 0 20px;
  }
  @nest &__btn{
  	font-size: 12px;
  	color: #fff;
  	border: 0;
  	outline: none;
  	float: right;
  	min-height: 35px;
  	background: #915189;
  	border-radius: 20px;
  	padding: 0 20px;
  	transition: opacity 300ms;
  	@nest &:hover{
  		opacity: .7;
  	}
  }
}