/* delivery-block */
.delivery-block{
	width: 320px;
	height: 320px;
	display: block;
	margin: auto;
	padding-top: 80px;
	margin-top: 25px;
	position: relative;
	background: #fff;
  border-radius: 50%;
  box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.25);
  @nest &:after{
  	position: absolute;
  	content: '';
  	left: 50%;
  	top: 50%;
  	transform: translate(-50%, -50%);
  	width: 110%;
  	height: 110%;
  	border-radius: 50%;
  	border: 1px dashed #a1a1a1;
  }
  @media (max-width: 767px){
  	width: 250px;
  	height: 250px;
  	padding-top: 40px;
  }
  @nest &__image{
  	display: block;
  	margin: auto;
  }
  @nest &__title{
  	font-size: 40px;
  	color: #6c6c6c;
  	text-align: center;
  	max-width: 200px;
  	display: block;
  	margin: 10px auto 0;
  	letter-spacing: -2px;
  	font-family: var(--font);
  	@media (max-width: 767px){
  		font-size: 30px;
  	}
  }
}