/* present-block */
.present-block{
	width: 100%;
	min-height: 365px;
	max-width: 390px;
	border-radius: 5px;
	position: relative;
	@media (max-width: 991px){
		margin: auto;
		margin-top: 20px;
		@media (max-width: 767px){
			min-height: 250px;
		}
	}
	@nest &--mod{
		max-width: 225px;
		margin: 20px 0 0;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 991px){
			margin-top: 0;
		}
		@media (max-width: 767px){
			display: none;
		}
		@nest & .present-block__title--blue{
			top: auto;
			bottom: 15px;
			left: 10px;
			font-size: 22px;
			line-height: 26px;
			transform: translateX(0);
		}
	}
	@nest &__title{
		position: absolute;
		content: '';
		@nest &--gray{
			top: 20px;
			left: 20px;
			font-size: 30px;
			line-height: 27px;
			color: #767676;
			font-family: var(--font);
		}
		@nest &--pink{
			top: 20px;
			left: 20px;
			font-size: 32px;
			color: #a52d6b;
			line-height: 30px;
			font-family: var(--font);
		}
		@nest &--blue{
			top: 20px;
			left: 50%;
			transform: translateX(-50%);
			text-align: center;
			font-size: 42px;
			line-height: 50px;
			font-family: var(--font);
			color: #8292aa;
			@media (max-width: 767px){
				font-size: 35px;
			}
		}
		@nest &-inner{
			display: inline-block;
			background: #fff;
			padding: 2.5px 10px;
			@nest &--italic{
				font-style: italic;
			}
		}
	}
}