/* kit-plus */
.kit-plus{
	position: absolute;
	content: '';
	top: 50%;
	right: -45px;
	width: 58px;
	height: 58px;
	z-index: 10;
	background: #915189;
	border-radius: 50%;
	font-size: 60px;
	color: #fff;
	line-height: 58px;
	text-align: center;
	transform: translateY(-50%);
	@nest &:after{
		position: absolute;
		content: '';
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		background: resolve('plus.png');
		width: width('plus.png');
		height: height('plus.png');
	}
	@media (max-width: 991px){
		right: -35px;
	}
	@media (max-width: 550px){
		bottom: -35px;
		right: auto;
		left: 50%;
		top: auto;
		transform: translateX(-50%);
	}
}