/* tovars-block */
.tovars-block{
	max-width: 220px;
	margin: auto;
	font-size: 0;
	cursor: pointer;
	position: relative;
	border-radius: 5px;
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.18);
  @nest &:hover{
  	@nest & .tovars-hidden{
  		opacity: 1;
  	}
  }
	@nest &__image{
		
	}
	@nest &__content{
		padding: 15px 20px 20px;
		background: #fff;
	}
	@nest &__title{
		font-size: 18px;
		color: #323232;
		font-weight: 600;
		margin: 0;
	}
	@nest &__price{
		font-size: 18px;
		color: #d94893;
		margin: 7px 0 0;
		font-weight: 700;
	}
}