/* categories */
.categories{
	padding-bottom: 10px;
	border-bottom: 1px dashed #555;
	@nest &__item{
		display: none;
		@nest &:checked + .categories__label:before{
			border: 0;
			background: #ad76a6;
			box-shadow: inset 0px 2px 0px 0px rgba(0, 0, 0, 0.16);
		}
		@nest &:checked + .categories__label:after{
			display: block;
		}
	}
	@nest &__label{
		display: block;
		cursor: pointer;
		position: relative;
		font-size: 14px;
		color: #2e2e2e;
		padding-left: 25px;
		margin-bottom: 15px;
		@nest &:before{
			position: absolute;
			content: '';
			left: 0;
			top: 50%;
			width: 17px;
			height: 17px;
			border-radius: 3px;
			border: 1px solid #bfbfbf;
			transform: translateY(-50%);
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 5px;
			top: 50%;
			display: none;
			transform: translateY(-50%);
			background: resolve('ok.png');
			width: width('ok.png');
			height: height('ok.png');
		}
	}
	@nest &__reset{
		font-size: 11px;
		color: #915189;
		font-weight: 600;
		border: 0;
		outline: none;
		padding: 0;
		margin: 20px 0 0;
		background: transparent;
		@nest & span{
			padding-right: 7px;
			text-decoration: underline;
		}
		@nest &:hover{
			@nest & span{
				text-decoration: none;
			}
		}
	}
	@nest &__btn{
		height: 36px;
		font-size: 12px;
		color: #fff;
		border: 0;
		clear: both;
		outline: none;
		display: block;
		padding: 0 20px;
		font-weight: 600;
		border-radius: 17px;
		background: #915189;
		margin: 20px 0 0;
		transition: background 300ms;
		@nest &:hover{
			background: #6d3666;
		}
		@nest &--more{
			margin: auto;
			@media (max-width: 991px){
				margin-top: 25px;
			}
		}
	}
}