/* catalog-content */
.catalog-content{
	@nest &__inner{
		margin: 15px 0 0;
		@nest & .no-padding-right{
			@media (max-width: 1199px){
				padding-right: 16px !important;
			}
		}
		@nest & .no-padding-left{
			@media (max-width: 1199px){
				padding-left: 16px !important;
			}
		}
		@nest & .tovars-block{
			margin-bottom: 25px;
			@media (max-width: 991px){
				margin-top: 25px;
				margin-bottom: 0;
			}
		}
	}
	@nest & .inner-nav{
		width: 100%;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 512px){
			display: block;
		}
	}
}