/* footer-adress */
.footer-adress{
	max-width: 135px;
	margin-right: 30px;
	display: inline-block;
	vertical-align: top;
	text-align: left;
	@nest &__city{
		font-size: 12px;
		color: #fff;
		margin: 0 0 10px;
	}
	@nest &__link{
		color: #fff;
		font-size: 12px;
		display: inline-block;
		@nest &:hover{
			text-decoration: none;
		}
	}
}