/* select */
.select{
	position: relative;
	display: inline-block;
	@nest &__current{
		cursor: pointer;
		font-size: 12px;
		color: #373737;
		font-weight: 600;
		padding-right: 13px;
		position: relative;
		@nest &:after{
			position: absolute;
			content: '';
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('tr.png');
			width: width('tr.png');
			height: height('tr.png');
		}
		@nest &:hover{
			text-decoration: underline;
		}
	}
	@nest &__list{
		position: absolute;
		content: '';
		top: 100%;
		left: 0;
		z-index: 10;
		display: none;
		padding: 0;
		margin: 0;
		list-style: none;
		background: #fbfbfb;
	}
	@nest &__item{
		font-size: 12px;
		color: #373737;
		cursor: pointer;
		margin: 3px;
		@nest &:hover{
			text-decoration: underline;
		}
	}
}