/* select-sort */
.select-sort{
	border: 1px solid #cca2c7;
	border-radius: 3px;
	@nest &--mod{
		border: 0;
	}
	@nest & .select__current{		
		color: #fff;
		font-size: 13px;
		display: block;
		padding: 9px 45px 9px 9px;
		border-top-left-radius: 3px;
		border-top-right-radius: 3px;
		@nest &:after{
			right: 12px;
			background: resolve('tr3.png');
			width: width('tr3.png');
			height: height('tr3.png');
		}
		@nest &--bg{
			color: #1c1c1c;
			background: #fff;
			border: 0;
			@nest &:after{
				background: resolve('tr2.png');
				width: width('tr2.png');
				height: height('tr2.png');
			}
		}
		@nest &:hover{
			color: #915189;
			background: #dfc4dc;
			text-decoration: none;
		}
	}
	@nest & .select__list{
		width: 100%;
		background: #fff;
		color: #1c1c1c;
		text-align: left;
	}
	@nest & .select__item{
		padding: 9px 7px;
		margin: 0;
		@nest &:hover{
			color: #915189;
			background: #dfc4dc;
			text-decoration: none;
		}
	}
}