/* inner-content */
.inner-content{
	position: relative;
	padding-bottom: 60px;
	@nest &:after{
		position: absolute;
		content: '';
		top: 100%;
		left: 0;
		right: 0;
		width: 100%;
		background: resolve('shadow.png');
		height: height('shadow.png');
	}
	@nest &__text{
		font-size: 16px;
		color: #444;
		line-height: 22px;
		margin: 0 0 25px;
		@nest & b{
			font-weight: 700;
			margin-top: 15px;
			display: inline-block;
		}
	}
}