/* card-table */
.card-table{
	display: table;
	width: 100%;
	margin: 35px 0 0;
	@nest &__row{
		display: table-row;
	}
	@nest &__col{
		width: 85%;
		font-size: 16px;
		color: #6b6b6b;
		padding: 10px 0;
		display: table-cell;
		border-bottom: 1px solid #e8e8e8;
		@media (max-width: 767px){
			font-size: 13px;
		}
		@nest &--right{
			width: 15%;
		}
		@nest &--title{
			color: #915189;
			font-weight: 600;
			font-family: var(--font2);
		}
	}
}