/* history-block */
.history-block{
	font-size: 0;
	clear: fix;
	margin-bottom: 15px;
	padding-bottom: 20px;
	border-bottom: 1px solid #d6d6d6;
	@nest &__left{
		width: 50%;
		float: left;
		@media (max-width: 1199px){
			float: none;
			width: 100%;
		}
	}
	@nest &__right{
		float: right;
		width: 40%;
		@media (max-width: 1199px){
			float: none;
			width: 100%;
		}
	}
	@nest &__col{
		padding: 25px 15px 15px;
		display: table-cell;
		vertical-align: top;
		border-right: 1px solid #d6d6d6;
		@nest &--sm{
			width: 20%;
		}	
		@nest &--md{
			width: 25%%;
		}
		@nest &--lg{
			width: 55%%;
		}
		@nest &--border{
			border-right: 0;
		}
		@media (max-width: 1199px){
			width: 100%;
			display: block;
			padding: 10px;
		}
	}
	@nest &__date{
		font-size: 14px;
		color: #383838;
		font-style: italic;
		font-family: var(--font);
	}
	@nest &__number{
		font-size: 14px;
		color: #444;
	}
	@nest &__item{
		font-size: 14px;
		color: #383838;
		display: block;
		margin-bottom: 25px;
		@nest &--right{
			text-align: right;
			@media (max-width: 1199px){
				text-align: left;
			}
		}
	}
	@nest &__price{
		font-size: 18px;
		color: #d94893;
		font-weight: 700;
		display: block;
		margin-top: -3px;
	}
	@nest &__btn{
		width: 100%;
		border: 0;
		outline: none;
		display: block;
		color: #fff;
		font-weight: 700;
		margin-top: -7px;
		line-height: 30px;
		text-align: center;
		font-size: 11px;
		min-height: 30px;
		border-radius: 3px;
		background: #915189;
		text-transform: uppercase;
		transition: opacity 300ms;
		@nest &--acc{
			background: #40b96d;
		}
		@media (max-width: 1199px){
			display: inline-block;
			width: auto;
			padding: 0 10px;
		}
	}
	@nest &__show{
		font-size: 14px;
		color: #383838;
		cursor: pointer;
		font-style: italic;
		font-family: var(--font);
		padding-right: 35px;
		position: relative;
		@nest &:after{
			position: absolute;
			content: '';
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('lk1.png');
			width: width('lk1.png');
			height: height('lk1.png');
		}
		@nest &:hover{
			text-decoration: underline;
		}
		@nest &--active{
			@nest &:after{
				background: resolve('lk2.png');
				width: width('lk2.png');
				height: height('lk2.png');
			}
		}
	}
}