/* reviews-slider */
.reviews-slider{
	@nest &__item{
		outline: none;
	}
	@nest & .slick-arrow{
		position: absolute;
		content: '';
		top: -65px;
		font-size: 0;
		border: 0;
		outline: none;
	}	
	@nest & .slick-prev{
		left: 125px;
		background: resolve('left.png');
		width: width('left.png');
		height: height('left.png');
	}
	@nest & .slick-next{
		left: 170px;
		background: resolve('right.png');
		width: width('right.png');
		height: height('right.png');
	}
	@nest &__inner{
		padding: 30px;
		background: resolve('rev.svg');
		-webkit-background-size: cover;
		background-size: cover;
	}
	@nest &__text{
		font-size: 15px;
		color: #373737;
		line-height: 24px;
		margin-bottom: 25px;
	}
	@nest &__bot{
		font-size: 12px;
		line-height: 18px;
		color: #373737;
		font-weight: 700;
		margin: 0 0 0 30px;
	}
	@nest &__block{
		max-width: 380px;
		margin: auto;
	}
}