/* login */
.login{
	width: 100%;
	font-size: 13px;
	color: #373737;
	font-weight: 600;
	line-height: 20px;
	max-width: 170px;
	position: relative;
	line-height: 20px;
	padding: 25px 0 20px 75px;
	display: inline-block;
	vertical-align: top;
	text-transform: uppercase;
	border-right: 1px solid #d6d6d6;
	@nest &:after{
		position: absolute;
		content: '';
		left: 25px;
		top: 50%;
		transform: translateY(-50%);
		background: resolve('login.png');
		width: width('login.png');
		height: height('login.png');
	}
	@nest &:hover{
		text-decoration: none;
	}
	@media (max-width: 512px){
		font-size: 11px;
		border-right: 0;
	}
}