/* nav-list */
.nav-list{
	padding: 0;
	margin: 0;
	position: relative;
	padding-right: 100px;
	list-style: none;
	@media (max-width: 991px){
		padding-right: 0;
		margin-top: 20px;
	}
	@nest &__item{
		display: table-cell;
		vertical-align: top;
		position: relative;
		@nest &:hover{
			@nest & .dropdown-list{
				display: block;
				opacity: .9;
			}
		}
		@media (max-width: 991px){
			display: block;
			text-align: center;
		}
	}
	@nest &__link{
		font-size: 12px;
		color: #fff;
		display: inline-block;
		font-weight: 700;
		text-decoration: none;
		text-transform: uppercase;
		padding: 10px 10px 9px 14px;
		transition: all 300ms;
		@nest &:hover{
			color: #915189;
			background: #fff;
		}
		@media (max-width: 1199px){
			font-size: 9px;
		}
		@media (max-width: 991px){
			font-size: 14px;
		}
	}
}