/* insta */
.insta{
	margin: 35px 0 0;
	@nest &__title{
		font-size: 18px;
		color: #6b6b6b;
		font-weight: 600;
		margin: 0 0 25px;
	}
	@nest &__block{
		display: block;
		margin: auto;
		border-radius: 5px;
		max-width: 220px;
		background: #fff;
		padding: 10px 10px 30px;
		box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.15);
		@nest & img{
			display: block;
			margin: auto;
		}
	}
	@media (max-width: 991px){
		text-align: center;
	}
}