/* lk-block-form */
.lk-block-form{
	font-size: 0;
	@nest &__label{
		width: 100%;
		font-size: 15px;
		color: #444;
		max-width: 135px;
		display: inline-block;
		vertical-align: middle;
	}
	@nest &__inp{
		width: 100%;
		max-width: 200px;
		border: 0;
		min-height: 37px;
		background: #f3f3f3;
		border-radius: 3px;
		margin-bottom: 10px;
		box-shadow: inset 0px 1px 1px 0px rgba(0, 0, 0, 0.1);
		@media (max-width: 991px){
			display: block;
			max-width: 100%;
			margin-top: 5px;
		}
	}
}