/* header-nav */
.header-nav{
	padding: 0;
	margin: 0;
	list-style: none;
	text-align: left;
	display: inline-block;
	@nest &:first-child{
		margin-right: 25px;
	}
	@nest &__link{
		font-size: 12px;
		color: #915189;
		font-weight: 600;
		display: block;
		margin-bottom: 3px;
		text-decoration: none;
		@nest &:hover{
			text-decoration: underline;
		}
	}
}