/* backet */
.backet{
	font-size: 13px;
	color: #373737;
	max-width: 240px;
	font-weight: 600;
	position: relative;
	display: inline-block;
	vertical-align: top;
	padding: 25px 0 20px 75px;
	@nest &:after{
		position: absolute;
		content: '';
		left: 20px;
		top: 50%;
		transform: translateY(-50%);
		background: resolve('backet.png');
		width: width('backet.png');
		height: height('backet.png');
	}
	@nest &:hover{
		text-decoration: none;
	}
	@media (max-width: 512px){
		font-size: 11px;
	}
}