/* inner-nav */
.inner-nav{
	margin: 0;
	max-width: 225px;
	list-style: none;
	background: #fff;
  border-radius: 6px;
	padding: 20px 0 10px;
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.18);
  @media (max-width: 991px){
  	margin: auto;
  }
	@nest &__item{
		margin-bottom: 13px;
	}
	@nest &__link{
		font-size: 14px;
		color: #2e2e2e;
		display: block;
		padding: 6px 0 8px 27px;
		position: relative;
		text-decoration: none;
		transition: background 300ms, color 300ms;
		@nest &:after{
			position: absolute;
			content: '';
			width: 3px;
			height: 3px;
			background: #000;
			left: 18px;
			top: 50%;
			transform: translateY(-50%);
			transition: background 300ms;
		}
		@nest &:hover{
			color: #e0b8db;
			background: #915189;
			@nest &:after{
				background: #e0b8db;
			}
		}
	}
}