/* tovars-slider */
.tovars-slider{
	@nest &__item{
		outline: none;
	}
	@nest & .slick-arrow{
		position: absolute;
		content: '';
		font-size: 0;
		border: 0;
		padding: 0;
		outline: none;
		top: -65px;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
		@media (max-width: 512px){
			top: -90px;
		}
	}
	@nest & .slick-prev{
		left: 140px;
		background: resolve('left.png');
		width: width('left.png');
		height: height('left.png');	
	}
	@nest & .slick-next{
		left: 182px;
		background: resolve('right.png');
		width: width('right.png');
		height: height('right.png');
	}
	@nest &--arrows{
		@nest & .slick-prev{
			left: 45%;
		}
		@nest & .slick-next{
			left: 49%;
			@media (max-width: 1199px){
				left: 50%;
			}
			@media (max-width: 991px){
				left: 52%;
			}
			@media (max-width: 767px){
				left: 55%;
			}
			@media (max-width: 512px){
				left: 65%;
			}
		}
	}
	@nest &--arrows-sec{
		@nest & .slick-prev{
			left: 28%;
			@media (max-width: 1199px){
				left: 40%;
			}
			@media (max-width: 767px){
				left: 45%;
			}
		}
		@nest & .slick-next{
			left: 32%;
			@media (max-width: 1199px){
				left: 46%;
			}
			@media (max-width: 991px){
				left: 48%;
			}
			@media (max-width: 767px){
				left: 55%;
			}
			@media (max-width: 512px){
				left: 67%;
			}
		}
	}
}