/* footer */
.footer{
	font-size: 0;
	margin: 75px 0 0;
	padding: 35px 0 25px;
	background: #915189;
	@nest &__copy{
		font-size: 12px;
		color: #fff;
		max-width: 210px;
		padding-right: 30px;
		letter-spacing: -.6px;
		display: inline-block;
		vertical-align: top;
		text-align: left;
		@media (max-width: 767px){
			display: block;
			margin: auto;
			margin-bottom: 20px;
			text-align: center;
		}
	}
	@media (max-width: 1199px){
		text-align: center;
	}
	@media (max-width: 512px){
		text-align: left;
		padding-left: 10px;
	}
}