/* billboard-slider */
.billboard-slider{
	position: relative;
	margin-bottom: 20px;
	@nest &__item{
		outline: none;
		background: #fff;
		border-radius: 6px;
		position: relative;
		min-height: 365px;
		padding: 30px 0 0 30px;
		box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.25);
		@media (max-width: 767px){
			min-height: 250px;
		}
		@media (max-width: 512px){
			text-align: center;
			padding: 30px 0 40px;
		}
	}
	@nest &__desc{
		font-size: 17px;
		color: #a52d6b;
		margin: 0 0 10px;
		position: relative;
		z-index: 20;
		@media (max-width: 512px){
			font-size: 15px;
		}
	}
	@nest &__title{
		font-size: 57px;
		color: #767676;
		margin: 0;
		position: relative;
		z-index: 20;
		line-height: 51px;
		max-width: 280px;
		font-weight: 400;
		font-family: var(--font);
		@media (max-width: 767px){
			line-height: normal;
			font-size: 40px;
		}
		@media (max-width: 512px){
			font-size: 30px;
			margin: auto;
		}
	}
	@nest &__image{
		position: absolute;
		right: 25px;
		top: 50%;
		transform: translateY(-50%);
		@media (max-width: 1199px){
			max-width: 50%;
		}
		@media (max-width: 512px){
			position: static;
			transform: translate(0);
			display: block;
			margin: auto;
			margin-top: 10px;
		}
	}
	@nest & .slick-dots{
		position: absolute;
		content: '';
		padding: 0;
		margin: 0;
		left: 20px;
		bottom: 20px;
		list-style: none;
		@media (max-width: 512px){
			left: 50%;
			bottom: 10px;
			transform: translateX(-50%);
		}
	}
	@nest & li{
		display: inline-block;
		margin-right: 10px;
		@nest & button{
			width: 19px;
			height: 19px;
			outline: none;
			font-size: 0;
			border-radius: 50%;
			background: #915189;
			border: 3px solid #915189;
			transition: background 300ms;
			@nest &:hover{
				background: transparent;
			}
		}
		@nest &.slick-active{
			@nest & button{
				background: transparent;
			}
		}
	}
}