/* info-form */
.info-form{
	clear: fix;
	background: #fff;
	padding: 30px 20px 20px;
	box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.18);
	@media (max-width: 991px){
		padding: 20px;
		margin-bottom: 20px;
	}
	@nest &--bg{
		padding: 0;
		background: transparent;
		box-shadow: none;
		margin-top: 0;
		@nest & .info-form__block{
			padding: 0;
			margin: 0;
			border-bottom: 0;
		}
		@nest & .info-input-wrap{
			width: 80%;
			@media (max-width: 512px){
				width: 100%;
			}
		}
		@nest & .info-form__input{
			color: #444;
			font-size: 16px;
			box-shadow: none;
			background: transparent;
			border: 1px solid #d7d7d7;
			@media (max-width: 612px){
				font-size: 12px;
			}
			@nest &::placeholder{
				color: #444;
				font-size: 16px;
				@media (max-width: 612px){
					font-size: 12px;
				}
			}
		}
		@nest & .card-wrap{
			width: 80%;
			@media (max-width: 512px){
				width: 100%;
			}
		}
		@nest & .info-form__desc{
			width: 20%;
		}
		@nest & .info-form__input{
			width: 80%;
			@media (max-width: 512px){
				width: 100%;
			}
		}
		@nest & .info-form__input--size{
			width: 95px;
		}
		@nest & .info-input-wrap__left{
			padding-right: 0;
		}
	}
	@nest &__item{
		font-size: 0;
		clear: both;
		margin-bottom: 10px;
		@nest &--indent-top{
			margin-top: 35px;
		}
	}
	@nest &__desc{
		width: 22%;
		font-size: 16px;
		color: #444;
		padding-right: 15px;
		text-align: right;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 1199px){
			font-size: 14px;
		}
		@media (max-width: 767px){
			width: 100%;
			text-align: left;
			margin-bottom: 5px;
		}
		@nest & span{
			display: block;
			font-size: 12px;
			color: #9d9d9d;
		}
	}
	@nest &__input{
		width: 78%;
		height: 37px;
		border: 0;
		color: #8b8b8b;
		font-size: 14px;
		padding-left: 15px;
		border-radius: 3px;
		display: inline-block;
		vertical-align: middle;
		background: #f3f3f3;
		box-shadow: inset 0px 1px 1px 0px rgba(0, 0, 0, 0.1);
		@media (max-width: 767px){
			width: 100%;
		}
		@nest &--size{
			width: 95px;
		}
		@nest &--lg{
			width: 100%;
		}
	}
	@nest &__wrap{
		text-align: right;
	}
	@nest &__btn{
		border: 0;
		outline: none;
		font-weight: 700;
		font-size: 16px;
		color: #c3c3c3;
		margin-left: 10px;
		min-height: 45px;
		background: #f3f3f3;
		padding: 0 20px;
		text-transform: uppercase;
		transition: background 300ms;
		@nest &:hover{
			background: #fbfbfb;
		}
		@nest &--active{
			color: #507500;
			background: #fbca00;
			@nest &:hover{
				background: #ffd31c;
			}
		}
		@media (max-width: 512px){
			font-size: 13px;
		}
	}
	@nest &__block{
		margin-bottom: 30px;
		padding-bottom: 30px;
		border-bottom: 1px solid #ededed;
		@nest &--mod{
			margin-bottom: 0;
			border-bottom: 0;
		}
		@nest &-title{
			font-size: 24px;
			color: #444;
			float: left;
			width: 50%;
			margin-bottom: 25px;
			font-family: var(--font2);
			@media (max-width: 512px){
				width: 100%;
				float: none;
			}
			@nest &-descr{
				float: right;
				font-size: 14px;
				color: #444;
				width: 50%;
				margin-top: 6px;
				text-align: right;
				font-family: var(--font2);
				@nest & a{
					color: #915189;
					@nest &:hover{
						text-decoration: none;
					}
				}
				@media (max-width: 512px){
					width: 100%;
					float: none;
					display: block;
					text-align: left;
					margin-bottom: 20px;
				}
			}
		}
	}
	@nest &__price{
		font-size: 16px;
		color: #444;
		float: right;
		margin: 10px 40px 0 0;
	}
}