/* footer-right */
.footer-right{
	float: right;
	@media (max-width: 1199px){
		float: none;
		text-align: left;
		display: inline-block;
	}
	@media (max-width: 991px){
		margin-left: 20px;
	}
	@media (max-width: 512px){
		margin-top: 20px;
	}
	@nest &__number{
		font-size: 18px;
		color: #fff;
		margin: 0 0 3px;
		font-weight: 600;
		@nest &--icon{
			position: relative;
			@nest &:after{
				position: absolute;
				content: '';
				left: -30px;
				top: 50%;
				transform: translateY(-50%);
				background: resolve('footer.png');
				width: width('footer.png');
				height: height('footer.png');
			}
		}
		@nest &-inner{
			font-size: 12px;
			padding-left: 5px;
		}
	}
	@nest &__mail{
		font-size: 13px;
		color: #fff;
		font-weight: 600;
		display: block;
		margin: 5px 0;
	}
	@nest &__link{
		font-size: 13px;
		color: #fff;
		font-weight: 600;
		@nest &:hover{
			text-decoration: none;
		}
	}
	@nest &__social{
		margin: 10px 0 0;
		@nest &-title{
			font-size: 12px;
			color: #fff;
			margin-bottom: 10px;
			display: block;
		}
		@nest &-link{
			margin: 0 2px;
			display: inline-block;
			vertical-align: top;
			transition: opacity 300ms;
			@nest &:hover{
				opacity: .7;
			}
		}
	}
}