/* small-input */
.small-input{
	width: 165px;
	float: right;
	text-align: right;
	@media (max-width: 767px){
		width: 100%;
		float: none;
		margin-top: 10px;
	}
	@nest & .info-form__desc{
		width: auto;
		@media (max-width: 767px){
			width: 100%;
		}
	}
	@nest &--size{
		width: 100%;
	}
	@nest &__image{
		margin: 10px 12px 0 0;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 512px){
			display: none;
		}
	}
	@nest &__hyphen{
		font-size: 16px;
		color: #444;
		margin: 0 5px;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 512px){
			display: block;
		}
	}
	@media (max-width: 1199px){
		text-align: left;
	}
}