/* steps */
.steps{
	margin: 30px 0 0;
	padding-top: 85px;
	background: resolve('steps.jpg') no-repeat center center;
	-webkit-background-size: cover;
	background-size: cover;
	@nest &__inner{
		padding: 40px 0 0;
		background: color(#fff alpha(79%));
		@media (max-width: 1100px){
			padding-top: 10px;
		}
	}
}