/* delivery */
.delivery{
	min-height: 400px;
	padding: 30px 20px 0 30px;
	background: resolve('delivery.png') no-repeat center center;
	-webkit-background-size: cover;
	background-size: cover;
	@nest &__title{
		font-size: 32px;
		color: #444;
		margin: 0;
		font-family: var(--font2);
		@media (max-width: 1199px){
			font-size: 25px;
		}
	}
	@nest &__desc{
		font-size: 32px;
		color: #915189;
		font-weight: 700;
		display: block;
		font-family: var(--font2);
		@media (max-width: 1199px){
			font-size: 25px;
		}
	}
	@nest &__time{
		font-size: 19px;
		color: #915189;
		display: block;
		font-family: var(--font2);
		@media (max-width: 1199px){
			font-size: 17px;
		}
	}
}