/* kit-round */
.kit-round{
	width: 100%;
	margin-left: 60px;
	position: relative;
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	padding-top: 85px;
	background: resolve('kit.png') no-repeat center center;
	max-width: width('kit.png');
	height: height('kit.png');
	-webkit-background-size: contain;
	background-size: contain;
	@media (max-width: 1199px){
		margin-top: 40px;
		margin-left: 0;
		@nest & .kit-round__sign{
			left: 50%;
			top: -62px;
			transform: translate(-50%, 0);
		}
	}
	@nest &__old{
		font-size: 36px;
		color: #d991b6;
		display: block;
		font-weight: 600;
		line-height: normal;
		text-decoration: line-through;
		@media (max-width: 767px){
			font-size: 25px;
		}
	}
	@nest &__new{
		font-size: 36px;
		color: #d94893;
		font-weight: 700;
		display: block;
		margin-top: -10px;
		line-height: normal;
		@media (max-width: 767px){
			font-size: 25px;
		}
	}
	@nest &__desc{
		font-size: 18px;
		color: #d94893;
		display: block;
		margin: 20px 0 25px;
	}
	@nest &__link{
		font-size: 16px;
		color: #fff;
		font-weight: 600;
		padding: 12px 35px;
		text-decoration: none;
		border-radius: 25px;
		background: #915189;
		display: inline-block;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
	}
	@nest &__sign{
		position: absolute;
		content: '';
		font-size: 60px;
		color: #5b5b5b;
		left: -45px;
		top: 50%;
		transform: translateY(-50%);
	}
}