/* kit-slider */
.kit-slider{
	@media (max-width: 1199px){
		text-align: center;
	}
	@nest &__item{
		outline: none;
	}
	@nest & .slick-arrow{
		position: absolute;
		content: '';
		font-size: 0;
		border: 0;
		padding: 0;
		outline: none;
		top: -65px;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
		@media (max-width: 512px){
			top: -90px;
		}
	}
	@nest & .slick-prev{
		left: 50%;
		background: resolve('left.png');
		width: width('left.png');
		height: height('left.png');	
	}
	@nest & .slick-next{
		left: calc(50% + 45px);
		background: resolve('right.png');
		width: width('right.png');
		height: height('right.png');
	}
}